.main {
  padding: 10px;
  margin-right: 5px;
  flex: 1;
  overflow-x: hidden;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-auto-rows: minmax(min-content, max-content);
  gap: 10px;
}

.main-item {
  padding: 10px;
  border-radius: 8px;
  background-color: rgb(240, 240, 240);
  height: 100%;
  box-sizing: border-box;
  transition: background-color 0.1s;
  cursor: pointer;
  user-select: none;
}

.main-item-hyper {
  width: 100%;
  height: 100%;
}

.main-item:last-of-type {
  margin-bottom: 10px;
}

.main-item:hover {
  background-color: rgb(225, 225, 225);
}

.main-image-div {
  display: inline-block;
  width: 76px;
}

.main-image {
  height: 64px;
  width: 64px;
}

.main-content {
  display: inline-block;
  vertical-align: top;
}

.main-name {
  font-size: 16px;
  font-weight: bold;
  color: black;
}

.main-text {
  font-size: 16px;
  color: black;
}

.main-scroll-hint {
  position: fixed;
  display: flex;
  left: 0;
  bottom: 0;
  height: 25px;
  width: 100%;
  background: linear-gradient(transparent, rgba(14, 167, 255, 0.349));
  transition: opacity 0.5s;
}

.main-scroll-hint span {
  width: inherit;
  text-align: center;
  align-self: flex-end;
  font-size: 14px;
  text-shadow:
    -1px -1px 0 rgb(223, 237, 255),  
     1px -1px 0 rgb(223, 237, 255),
    -1px  1px 0 rgb(223, 237, 255),
     1px  1px 0 rgb(223, 237, 255);
}

.main-scroll-hint-enter-active,
.main-scroll-hint-enter-done,
.main-scroll-hint-exit {
  opacity: 1;
  visibility: visible;
}


.main-scroll-hint-exit-active {
  opacity: 0;
}
.main-scroll-hint-exit-done {
  opacity: 0;
  visibility: hidden;
}