.header {
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  background-color: rgb(210, 232, 255);
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 1;
  height: 50px;
  padding: 5px;
  box-sizing: border-box;
}

.header-logo {
  height: 40px;
  width: 40px;
  margin-right: 5px; 
}

.header-title {
  font-size: 20px;
  color: #444;
  flex: 1;
}

.header-btn {
  float: right;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 16px;
  cursor: pointer;
  user-select: none;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  margin-left: 5px;
  transition: background-color 0.2s;
}

.header-btn:hover {
  background-color: rgba(0, 0, 0, 0.15);
}

.header-aside-btn {
  font-size: 20px;
}