.info {
  position: absolute;
  background: rgba(0, 0, 0, 0.3);
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.25s;
}

.info-enter-active,
.info-enter-done {
  visibility: visible;
  opacity: 1;
}

.info-exit {
  visibility: visible;
  opacity: 1;
}
.info-exit-active {
  opacity: 0;
}
.info-exit-done {
  visibility: hidden;
  opacity: 0;
}

.info-div {
  background: #fafafa;
  height: fit-content;
  max-height: 95vh;
  width: fit-content;
  max-width: 95vh;
  border-radius: 10px;
  box-shadow: 0 0 5px rgb(87, 87, 87);
  padding: 20px;
  box-sizing: border-box;
  overflow-y: scroll; 
  overflow-x: auto; 
}

.info-content {
  margin: 0;
  font-size: 16px;
}